// src/utils/auth.js

import { Message } from 'element-ui'

import Request from '../request'

const TOKEN = 'crm-token'
const OLD_CRM_TOKEN = 'old-crm-token'

export const setToken = (token) => {
  const value = { Authorization: `Bearer ${token}` }
  localStorage.setItem(TOKEN, JSON.stringify(value))
}

export const getToken = () => {
  const value = localStorage.getItem(TOKEN)
  return value ? JSON.parse(value) : {}
}

export const removeToken = () => {
  localStorage.removeItem(TOKEN)
}

export const logout = async () => {
  try {
    await Request.auth.delete('/v1/auth/admin/token')
  } finally {
    removeToken()
    location.reload()
  }
}

export const fetchCrmToken = async () => {
  const { code, data } = await Request.auth.get('/v1/auth/admin/token-ugly-crm')

  if (code !== 0) throw new Error('获取用户凭证失败')
  setCrmToken(data)
}

export const setCrmToken = (value) => {
  localStorage.setItem(OLD_CRM_TOKEN, JSON.stringify(value))
}

const DEFAULT_LOGOUT_MESSAGE = '登录信息已过期，需要重新登录'
export const logoutWithErrorMessage = (message = DEFAULT_LOGOUT_MESSAGE) => {
  const duration = 2000
  const opts = {
    duration,
    message: `${message}，即将跳转到登录页面...`,
    type: 'error',
  }

  Message(opts)
  setTimeout(logout, duration)
}
