// src/micro/apps.js

import store from '../store'

const apps = [
  {
    name: 'CrmPoster',
    entry: process.env.VUE_APP_CRM_POSTER,
    container: '#frame',
    activeRule: '/#/poster',
    title: '个性化海报',
    path: '/poster',
    props: { store },
  },
  {
    name: 'CrmGroupon',
    entry: process.env.VUE_APP_CRM_GROUPON,
    container: '#frame',
    activeRule: '/#/groupon',
    title: '拼团',
    path: '/groupon',
    props: { store },
  },
]

export default apps
