// src/store/modules/config.js

import Request from '../../request'

export default {
  state: {
    data: null,
  },

  mutations: {
    SET_CONFIG_DATA(state, data) {
      state.data = data
    },
  },

  actions: {
    async getConfigData({ commit }) {
      try {
        const { code, data } = await Request.auth.get(
          '/v1/auth/admin/crm-sys-cfg'
        )

        if (code !== 0) throw new Error('获取配置信息失败')
        commit('SET_CONFIG_DATA', data)
      } catch (error) {
        throw new Error(error)
      }
    },
  },
}
