// src/store/modules/user.js

import Request from '../../request'

const USER_DATA_QUERY =
  'departments(title,leaderId,parent,children,dingtalkId,leader,id),realname,nickname,id,mobile,gender,roles(id,name,group(name))'
const USER_ROLES_QUERY = 'service,route,title,verb,id'

export default {
  state: {
    data: null,
    roles: null,
    ROLES_MAP: new Set(),
  },

  mutations: {
    SET_USER_DATA(state, data) {
      state.data = data
    },

    SET_USER_ROLES(state, roles) {
      if (roles === 'super') {
        state.roles = roles
        return
      }

      const ROLES_MAP = new Set()
      state.roles = roles.map((r) => {
        const { service, verb, route } = r
        const role = String(`[${service}][${verb}][${route}]`).toLowerCase()
        ROLES_MAP.add(role)
        return role
      })

      state.ROLES_MAP = ROLES_MAP
    },
  },

  actions: {
    async getUserData({ commit }) {
      try {
        const { code, data } = await Request.auth.get('/v1/auth/admin', {
          params: { __fields: USER_DATA_QUERY },
        })

        if (code !== 0) throw new Error('获取用户信息失败')
        commit('SET_USER_DATA', data)
      } catch (error) {
        throw new Error(error)
      }
    },

    async getUserRoles({ commit }) {
      try {
        const params = { __fields: USER_ROLES_QUERY }
        const {
          code,
          data,
          more,
        } = await Request.auth.get('/v1/auth/admin-http-apis', { params })

        if (code !== 0) throw new Error('获取用户权限失败')
        if (more.super) return commit('SET_USER_ROLES', 'super')
        if (Array.isArray(data) && !!data.length)
          return commit('SET_USER_ROLES', data)

        throw new Error('该用户没有任何权限')
      } catch (error) {
        throw new Error(error)
      }
    },
  },
}
